@accentColor: #2196f3;
@textColor: #333;

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  color: @textColor;
}

a {
  color: unset;
  text-decoration: none;
  font-weight: bold;
}

.links {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 2;
}

.hero {
  background-color: @accentColor;
  height: 300px;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 70%);
  padding: 30px 14px 0 14px;
  color: white;

  .title {
    margin-top: 30px;
    font-size: 50px;
  }

  .sub-title {
  }
}

.blue {
  color: white;
  background-color: @accentColor;
  background-image: url("images/grid.png");
  background-repeat: repeat;

}

section {
  margin: 0;
  padding: 0 10px;
  position: relative;


  &.middle {
    padding-top: 70px;
    padding-bottom: 70px;
    clip-path: polygon(0 0%, 100% 20%, 100% 90%, 0 100%);
  }

  &.install {
    padding-top: 80px;
    padding-bottom: 100px;
    clip-path: polygon(0 20%, 100% 0%, 100% 70%, 0 100%);
  }

  h1 {
    margin-top: 0;
    font-weight: 400;
  }

  h2 {
    font-weight: 400;
    font-size: 23px;
    margin-top: 10px;
    margin-bottom: 5px;
    font-weight: 400;
  }

  video {
    display: block;
    margin: 5px auto;
    width: 60%;
    border-radius: 10px;
    box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.1), 0px 2px 4px -1px rgba(0, 0, 0, 0.06);
  }

  p {
    margin-top: 0;
  }

  code {

    @background: #ccc;
    display: block;
    border-radius: 5px;
    padding: 10px;
    background-color: @background;
    border: 1px solid darken(@background, 50%);
    color: darken(@background, 50%);
    margin: 10px;
  }
}

footer {
  margin-top: 50px;
  padding: 15px 10px 5px 10px;
  font-size: 15px;

  a {
    color: @textColor;

    &:visited {
      color: @textColor;

    }
  }
}

.icon {
  color: darken(@accentColor, 30%);
  animation: 1s fade-in ease-in-out;
  transition: all 1s ease-in-out;
  opacity: 0.2;

  &.remove {
    opacity: 0;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.2;
  }
}


@media only screen and (min-width: 500px) {
  section {
    video {
      width: 50%;
    }
  }
}

@media only screen and (min-width: 700px) {
  section {
    padding: 0 30px;

    video {
      width: 30%;
    }
  }
}

@media only screen and (min-width: 900px) {
  .hero {
    padding-left: 100px;
  }

  section {
    padding: 0 150px;

    h2 {
      margin-top: 40px;
    }

    video {
      width: 20%;
    }
  }
}

@media only screen and (min-width: 1100px) {

  section {
    .content {
      width: 800px;
      margin: 0 auto;

      .text-video{
        //display: flex;

        video{
          margin: 50px;
          width: 200px;
        }
      }
    }
  }
}